import React from "react"
import { Tag } from "antd"

const styles = {
  article: {
    width: "25%",
    height: "20%",
    maxWidth: "300px",
    maxHeight: "200px",
    margin: "3rem 2rem",
  },
  innerContainer: {
    width: "100%",
    height: "100%",
    paddingBottom: "5%",
  },
  image: {
    width: "100%",
    height: "100%",
  },
}

const TripTile = ({ image, title, style, days, price }) => {
  return (
    <article className="trip-tile">
      <div className="trip-tile-image-container">
        <img src={image} alt="" style={styles.image} />
      </div>
      <div>
        <Tag color="#108ee9">{`${days} Days`}</Tag>
        <h3 className="trip-title">{title}</h3>
        <h4 className="trip-price">₹{price}</h4>
      </div>
    </article>
  )
}

export default TripTile
